.header_div{
  color: whitesmoke;
  background-color: darkolivegreen;
  margin:auto;
}

.header_h2{
  color: whitesmoke;
  font-family: 'Courier New', Courier, monospace;
  font-weight: bold;
  margin: 0;
  background-color: darkolivegreen;
  font-size: calc(2vw + 2vh);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  position: relative;
  text-align: center;
}

a:hover {
    text-decoration: none;
}

.profile-pic {
    height: 4vmin;
    margin-right: 10px;
    border-radius: 50px;
}

.jumbotron{
  padding: 1.5rem 2rem;
}

div.media {
    margin-top: 30px;
    margin-bottom: 10px;
}

div.media .media-heading .jobTotalDuration {
    color: #666;
    font-size: 14px;
    display: block;
}

div.media img {
    height: 10vmin;
    margin-right: 10px;
    border-radius: 50px;
}

div.media .jobDuration {
    color: #444;
    display: block;
}

div.media .jobLocation {
    color: #666;
}

.formLabel {
    font-weight: bold;
}

.certificateLink {
    color: cornflowerblue;
}

footer {
    margin-top: 20px;
    margin-bottom: 10px;
    border-top: #444444;
    text-align: center;
}

footer a {
    color: black;
    font-size: 30px;
    margin-right: 10px;
    margin-left: 10px;
}

[style*="--aspect-ratio"] > :first-child {
  width: 100%;
}
[style*="--aspect-ratio"] > img {  
  height: auto;
} 
@supports (--custom:property) {
  [style*="--aspect-ratio"] {
    position: relative;
  }
  [style*="--aspect-ratio"]::before {
    content: "";
    display: block;
    padding-bottom: calc(100% / (var(--aspect-ratio)));
  }  
  [style*="--aspect-ratio"] > :first-child {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
  }  
}

html body{
  margin: 0px;
}

.title_profile{
  font-size: calc(3vw + 3vh);
  font-weight: lighter;
}

.collapse:not(.show) {
  display: block;
  max-height: 120px;
}

.list_period li{
  margin: 10px 0px;
}

button{
  border: none;
  box-shadow: none !important;
}

table {
  border-collapse: separate;
}
table {
  border-spacing: 0;
  border: 1px solid black;
  text-align: center;
}
table  tr {
    width: auto;
}

table td, th{
    margin: 0;
    padding: 0.5rem;
    border-bottom: 1px solid black;
    border-right: 1px solid black;
    text-align: center;
  }
